"use client";

import React, { useState } from "react";
import { formatDollar } from "@/shared/utils/currency.util";
import { DateIcon } from "@/assets/svg/svg_icon";
import ReceiptModal from "../modal/receipt.modal";
import { useRouter } from "next/navigation";
import { orderDetailsRoute } from "@/core/routes/routeNames";
import { ordersData, getStatusTextColor } from "../data/orders.data";

const OrderHistoryPage = () => {
    const [activeTab, setActiveTab] = useState<"all" | "ongoing" | "completed">("all");
    const [receiptModal, setReceiptModal] = useState(false);
    const router = useRouter();

    const filteredOrders = ordersData.filter(order => {
        if (activeTab === "all") return true;

        const hasOngoing = order.items.some(item =>
            ["Processing", "Shipped", "Confirmed"].includes(item.status)
        );
        const hasCompleted = order.items.every(item =>
            ["Delivered", "Cancelled"].includes(item.status)
        );

        if (activeTab === "ongoing") return hasOngoing;
        if (activeTab === "completed") return hasCompleted;
        return true;
    });

    const handleViewReceipt = () => {
        setReceiptModal(true);
    };

    return (
        <div className="bg-white p-6 rounded-2xl border border-gray-200">
            {/* Header */}
            <div className="mb-6 border-b border-gray-200 pb-4">
                <h1 className="text-3xl font-semibold text-gray-900 mb-1">Order History</h1>
                <p className="text-normal text-gray-800">View and manage your order list.</p>
            </div>

            {/* Tabs */}
            <div className="border-b border-gray-200 mb-6">
                <div className="flex gap-8">
                    <button
                        onClick={() => setActiveTab("all")}
                        className={`pb-3 text-lg font-medium transition-colors relative cursor-pointer ${activeTab === "all"
                            ? "gradient-text"
                            : "text-gray-600 hover:text-gray-900"
                            }`}
                    >
                        All Order ({ordersData.length})
                        {activeTab === "all" && (
                            <div className="absolute bottom-0 left-0 right-0 h-0.5 bg-gradient" />
                        )}
                    </button>
                    <button
                        onClick={() => setActiveTab("ongoing")}
                        className={`pb-3 text-lg font-medium transition-colors relative cursor-pointer ${activeTab === "ongoing"
                            ? "gradient-text"
                            : "text-gray-600 hover:text-gray-900"
                            }`}
                    >
                        Ongoing ({ordersData.filter(o => o.items.some(i => ["Processing", "Shipped", "Confirmed"].includes(i.status))).length})
                        {activeTab === "ongoing" && (
                            <div className="absolute bottom-0 left-0 right-0 h-0.5 bg-gradient" />
                        )}
                    </button>
                    <button
                        onClick={() => setActiveTab("completed")}
                        className={`pb-3 text-lg font-medium transition-colors relative cursor-pointer ${activeTab === "completed"
                            ? "gradient-text"
                            : "text-gray-600 hover:text-gray-900"
                            }`}
                    >
                        Completed ({ordersData.filter(o => o.items.every(i => ["Delivered", "Cancelled"].includes(i.status))).length})
                        {activeTab === "completed" && (
                            <div className="absolute bottom-0 left-0 right-0 h-0.5 bg-gradient" />
                        )}
                    </button>
                </div>
            </div>

            {/* Order Count */}
            <h2 className="text-normal font-medium text-gray-900 mb-6">
                {activeTab.charAt(0).toUpperCase() + activeTab.slice(1)} Order - {filteredOrders.length} total orders
            </h2>

            {/* Orders List */}
            <div className="space-y-6">
                {filteredOrders.map((order) => (
                    <div
                        key={order.orderId}
                        className="bg-[#F9FAFB] border border-[#E5E7EB] rounded-2xl p-6"
                    >
                        {/* Order Header */}
                        <div className="flex items-start justify-between">
                            <div className="flex-1">
                                <div className="flex items-center gap-4 mb-2">
                                    <h3 className="font-medium text-lg text-gray-900">
                                        Order ID: #{order.orderId}
                                    </h3>
                                    {/* Progress Bar */}
                                    <div className="flex-1 flex items-center gap-3">
                                        <div className="flex-1 h-1.5 bg-gray-200 rounded-full overflow-hidden max-w-xs">
                                            <div
                                                className={`h-full rounded-full ${order.progress >= 80 ? "bg-[#077D58]" : "bg-[#F8902E]"
                                                    }`}
                                                style={{ width: `${order.progress}%` }}
                                            />
                                        </div>
                                        <span className="text-sm font-medium text-gray-800">
                                            Order is {order.progress}% complete
                                        </span>
                                    </div>
                                </div>
                                <div className="flex items-center gap-2 text-gray-600 mb-4">
                                    <DateIcon />
                                    <span>Order date: {order.orderDate}</span>
                                </div>
                            </div>

                            <span className="text-2xl font-medium text-gray-900">
                                {formatDollar(order.total)}
                            </span>
                        </div>

                        {/* Order Items */}
                        <div className="flex justify-between">
                            <div className="flex gap-3">
                                {order.items.map((item) => (
                                    <div key={item.id} className="shrink-0">
                                        <div className="w-20 h-20 bg-gray-200 border border-gray-300 rounded-xl mb-2"></div>
                                        <p className={`text-sm font-medium ${getStatusTextColor(item.status)} mb-0.5 shrink-0`}>
                                            Status: {item.status}
                                        </p>
                                        <p className="text-sm text-gray-600">Item no: {item.itemNo}</p>
                                        <p className="text-sm text-gray-600">QTY: {item.qty}</p>
                                    </div>
                                ))}
                            </div>

                            {/* Action Buttons */}
                            <div className="flex gap-3">
                                <button
                                    onClick={() => handleViewReceipt()}
                                    className="text-lg font-medium text-gray-700 hover:text-gray-900 transition-colors cursor-pointer"
                                >
                                    View Receipt
                                </button>
                                <button
                                    onClick={() => router.push(orderDetailsRoute.replace(":id", order.orderId))}
                                    className="text-lg font-medium gradient-text hover:text-orange-600 transition-colors cursor-pointer">
                                    View Details
                                </button>
                            </div>
                        </div>
                    </div>
                ))}
            </div>

            <ReceiptModal
                isOpen={receiptModal}
                onClose={() => setReceiptModal(false)}
            />
        </div>
    );
};

export default OrderHistoryPage;